 body {
   margin: 0 auto;
   font-family: Arial, sans-serif;
   width: 100%;
  
 }

 .hero {
   height: 100vh;
   background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
   background-size: cover;
   background-position: center;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   color: #fff;
   text-align: center;
 }

 .hero-title {
  font-size: 4vw;
    font-weight: 700;
    margin-bottom: 20px;
    font-family:Serif, Ineria;
 }

 .hero-subtitle {
  font-size: 2.5vw;
    font-weight: 500;
    margin-bottom: 40px;
 }
@media (max-width: 480px) {
  .hero-subtitle--small {
    font-size: 2.5rem;
  }
    .hero-title {
      font-size: 7vw;
      
    }
}
@media (max-width: 768px) {
  .hero-subtitle--small {
    font-size: 2.5rem;
  }

  .hero-title {
    font-size: 7vw;

  }
}
 .hero-button {
   padding: 15px 30px;
   background-color: #FC7901;
   color: #fff;
    border: 2px solid #FC7901;
   border-radius: 30px;
   font-size: 1.2rem;
   font-weight: 700;
   color: #fff;
   transition: all 0.3s ease;
   text-decoration: none;
 }
 @media (min-width: 1000px) {
  .hero-button {
       padding: 15px 50px; 
    background-color: #FC7901;
    border: 2px solid #FC7901;
    border-radius: 30px;
    color: #fff;
    font-size: 1.5rem;
    }
 }

 .hero-button:hover {
   background-color: transparent;
   color: #fff;
 }

