.image-overlay {
  position: relative;
   width: 100%;
  max-width: 320px;
   margin-bottom: 1rem;  
  margin: 0 10px 20px;

}

.image-overlay img {
  width: 100%;
  height: auto;
}

.overlay-text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 1.2rem;
}
